'use strict';

var Button = require('./button');

var button1 = new Button({
  text: 'button1',
  style: {
    'background-color': 'blue',
    'height': '50px',
    'border': 'none',
    'width': '100px'
  },
  onclick: function(button) {
    console.log(button); 
  }
});

button1.mountToElementById('my-button');

var button2 = new Button({
  text: 'button2',
  style: {
    'background-color': 'red',
    'height': '50px',
    'width': '100px',
    'border': 'none'
  },
  onclick: function(button) {
    console.log(button); 
  }
});
button2.mountToElementById('my-button');



